import React from 'react';
import styled from 'styled-components';
import { Row, Col } from 'antd';
import { useStaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

// utils

// assets

// actions

// components

// self-defined-components
const Container = styled.div`
  padding-top: 75px;
`;

const BlockTitle = styled.h1`
  font-size: 36px;
  text-align: center;
  color: #555555;
`;

const StyledImg = styled(Img)`
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.7;
  }
`;

const Ttile = styled.div`
  margin-top: 10px;
  font-family: 'ibm plex serif';
  font-size: 18px;
  color: #222222;
  text-align: center;
`;

const Date = styled.div`
  font-size: 15px;
  color: #555555;
  margin-top: 5px;
  text-align: center;
`;

const Blog = () => {
  const {
    allMarkdownRemark: {
      edges
    },
  } = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        sort: {
          order: DESC,
          fields: [frontmatter___date]
        },
        filter: {
          fileAbsolutePath: { regex: "/content\/blogs/" }
        },
        limit: 6
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              date(formatString: "MMM DD, YYYY")
              coverImage {
                id
                childImageSharp {
                  fluid {
                    ...GatsbyImageSharpFluid_withWebp_noBase64
                  }
                }
              }
              url
            }
          }
        }
      }
    }
  `);

  return (
    <Container>
      <BlockTitle>Blog</BlockTitle>
      <Row gutter={[16, 40]}>
        {edges.map(({
          node: {
            id,
            frontmatter: {
              title,
              date,
              coverImage,
              url,
            },
          },
        }) => (
          <Col key={id} md={8} sm={24} xs={24}>
            <a href={url} alt={url} target="_blank" rel="noreferrer noopener">
              <StyledImg
                sizes={{ ...coverImage.childImageSharp.fluid, aspectRatio: 1.5 }}
                alt={title}
              />
              <Ttile>{title}</Ttile>
              <Date>{date}</Date>
            </a>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default Blog;
