import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { useStaticQuery, graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faTwitter,
  faLinkedinIn,
  faMediumM,
} from '@fortawesome/free-brands-svg-icons'

// utils

// assets

// actions

// components

// self-defined-components
const Container = styled.div`
  padding-top: 75px;
  text-align: center;
  user-select: none;
`;

const BlockTitle = styled.h1`
  font-size: 36px;
  text-align: center;
  color: #555555;
`;

const StyledIconLink = styled.a`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  color: white;
  margin: 0 12px;
  transition: opacity 0.3s;

  &.twitter {
    background-color: #1da1f2;
  }

  &.linkedin {
    background-color: #0077b5;
  }

  &.medium {
    background-color: #3a3d4a;
  }

  &:hover {
    color: white;
    opacity: 0.7;
  }
`;

const RenderSocialLink = ({ name, url }) => {
  switch(name) {
    case 'twitter':
      return (
        <StyledIconLink href={url} className="twitter" target="_blank" rel="noreferrer noopener" aria-label="Twitter">
          <FontAwesomeIcon icon={faTwitter} size="2x" />
        </StyledIconLink>
      );
    case 'linkedin':
      return (
        <StyledIconLink href={url} className="linkedin" target="_blank" rel="noreferrer noopener" aria-label="LinkedIn">
          <FontAwesomeIcon icon={faLinkedinIn} size="2x" />
        </StyledIconLink>
      );
    case 'medium':
      return (
        <StyledIconLink href={url} className="medium" target="_blank" rel="noreferrer noopener" aria-label="Medium">
          <FontAwesomeIcon icon={faMediumM} size="2x" />
        </StyledIconLink>
      );
    default:
      return <></>;
  }
};

RenderSocialLink.propTypes = {
  name: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired,
};

const FindMe = () => {
  const data = useStaticQuery(graphql`
    query FindMeQuery {
      site {
        siteMetadata {
          title
          social {
            name
            url
          }
        }
      }
    }
  `);
  const { social } = data.site.siteMetadata;

  return (
    <Container>
      <BlockTitle>Find Me</BlockTitle>

      {social.map((data) => (
        <RenderSocialLink key={data.name} {...data} />
      ))}
    </Container>
  );
};

export default FindMe;
