import React from 'react';
import styled from 'styled-components';
import { useStaticQuery, graphql } from 'gatsby';
import { Layout, Row, Col } from 'antd';

// utils

// assets

// actions

// components
import FBLikeButton from './FacebookLikeButton';
import TwitterShareButton from './TwitterShareButton.js';
import LinkedInShareButton from './LinkedInShareButton.js';

// self-defined-components
const StyledFooter = styled(Layout.Footer)`
  padding: 75px 120px;
  background-color: white;
  user-select: none;

  @media (max-width: 992px) {
    padding-left: 40px;
    padding-right: 40px;
  }
`;

const SocialMediaButtons = styled.div`
  display: flex;
  justify-content: start;

  div {
    display: flex;
    align-items: center;
    margin-left: 6px;

    &:first-child {
      margin: 0;
    }
  }

  @media (max-width: 768px) {
    justify-content: center;
  }
`;

const Copyright = styled.div`
  font-size: 13px;
  line-height: 22px;
  color: #50555c;
  text-align: right;

  @media (max-width: 768px) {
    margin-top: 10px;
    text-align: center;
  }
`;

const AppFooter = () => {
  const {
    site: {
      siteMetadata: {
        title,
        author,
        siteURL,
      },
    },
  } = useStaticQuery(graphql`
    query AppFooterQuery {
      site {
        siteMetadata {
          title
          author
          siteURL
        }
      }
    }
  `);

  return (
    <StyledFooter>
      <Row justify="space-between">
        <Col md={12} sm={24} xs={24}>
          <SocialMediaButtons>
            <div>
              <FBLikeButton url={siteURL} />
            </div>
            <div>
              <TwitterShareButton text={title} />
            </div>
            <div>
              <LinkedInShareButton url={siteURL} />
            </div>
          </SocialMediaButtons>
        </Col>
        <Col md={12} sm={24} xs={24}>
          <Copyright>&copy; {new Date().getFullYear()} {author}</Copyright>
        </Col>
      </Row>
    </StyledFooter>
  );
};

export default AppFooter;
