import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Row, Col } from 'antd';
import { useStaticQuery, graphql } from "gatsby"

// utils

// assets

// actions

// components
import Album from './Album';

// self-defined-components
const Container = styled.div`
  padding-top: 75px;
`;

const BlockTitle = styled.h1`
  font-size: 36px;
  text-align: center;
  color: #555555;
  word-break: break-all;
`;

const ParticipatedEvent = ({ data }) => {
  const {
    allImageSharp: {
      edges: sharpEdges,
    },
    allMarkdownRemark: {
      edges: remarkEdges
    },
  } = useStaticQuery(graphql`
    query {
      allImageSharp(
        sort: {
          fields: fluid___originalName
        }
      ) {
        edges {
          node {
            fluid {
              ...GatsbyImageSharpFluid_withWebp_noBase64
            }
            parent {
              ... on File {
                relativeDirectory
              }
            }
          }
        }
      }
      allMarkdownRemark(
        sort: {
          order: DESC,
          fields: frontmatter___date
        },
        filter: {
          fileAbsolutePath: { regex: "/content\/participated_events/" }
        }
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              coverTitle
              description
              albumPath
            }
          }
        }
      }
    }
  `);

  return (
    <Container>
      <BlockTitle>Conferences/Meetings/topics that participated in the past</BlockTitle>

      <Row>
        {remarkEdges.map(({
          node: {
            id,
            frontmatter: {
              title,
              coverTitle,
              description,
              albumPath,
            },
          },
        }) => {
          const imageNodes = sharpEdges.filter((ele) => {
            return ele.node.parent.relativeDirectory === albumPath;
          });

          let fluid = {};
          const images = [];

          if (imageNodes.length > 0) {
            fluid = imageNodes[0].node.fluid;
            imageNodes.forEach(ele => images.push(ele.node.fluid.src));
          }

          return (
            <Col key={id} md={6} sm={24} xs={24}>
              <Album
                title={title}
                coverFluid={fluid}
                coverTitle={coverTitle}
                description={description}
                images={images}
              />
            </Col>
          );
        })}
      </Row>
    </Container>
  );
};

ParticipatedEvent.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.number.isRequired,
      title: PropTypes.string,
      coverTitle: PropTypes.string,
      description: PropTypes.string,
      images: PropTypes.arrayOf(PropTypes.string).isRequired,
      albumPath: PropTypes.string.isRequired,
    }),
  ),
};
ParticipatedEvent.defaultProps = {
  data: [],
};

export default ParticipatedEvent;
