import React from 'react';
import styled from 'styled-components';
import { Row, Col } from 'antd';
import { useStaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

// utils

// assets

// actions

// components

// self-defined-components
const Container = styled.div`
  padding-top: 75px;
`;

const BlockTitle = styled.h1`
  font-size: 36px;
  text-align: center;
  color: #555555;
`;

const LeftCol = styled(Col)`
  align-self: center;

  @media (max-width: 768px) {
    align-self: auto;
  }
`;

const Title = styled.div`
  font-family: 'ibm plex serif';
  font-size: 18px;
  color: #222222;

  @media (max-width: 768px) {
    margin-top: 10px;
  }
`;

const Subtitle = styled.div`
  font-size: 15px;
  font-style: italic;
  color: #555555;
`;

const LinkButton = styled.a`
  margin-top: 15px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1;
  color: #50555c;
  border: #50555c 2px solid;
  border-radius: 4px;
  display: inline-block;
  padding: 10px 20px;
  text-decoration: none;

  &:hover {
    color: #50555c;
    opacity: 0.8;
  }
`;

const StyledImg = styled(Img)`
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.7;
  }
`;

const Recent = () => {
  const {
    allMarkdownRemark: {
      edges
    },
  } = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        sort: {
          order: DESC,
          fields: [frontmatter___date]
        },
        filter: {
          fileAbsolutePath: { regex: "/content\/recents/" }
        },
        limit: 4
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              subtitle
              coverImage {
                id
                childImageSharp {
                  fluid {
                    ...GatsbyImageSharpFluid_withWebp_noBase64
                  }
                }
              }
              url
            }
          }
        }
      }
    }
  `);

  return (
    <Container id="recent">
      <BlockTitle>Recent</BlockTitle>
      {edges.map(({
        node: {
          id,
          frontmatter: {
            title,
            subtitle,
            coverImage,
            url,
          },
        },
      }) => (
        <Row key={id} style={{ marginTop: 35 }}>
          <LeftCol md={{ span: 14, order: 1 }} sm={{ span: 24, order: 2 }} xs={{ span: 24, order: 2 }}>
            <Title dangerouslySetInnerHTML={{ __html: title.replace(/\n/g, "<br />") }} />
            <Subtitle>{subtitle}</Subtitle>
            {url && <LinkButton href={url} alt={url} target="_blank" rel="noreferrer noopener">more...</LinkButton>}
          </LeftCol>
          <Col md={{ span: 9, order: 2, offset: 1 }} sm={{ span: 24, order: 1 }} xs={{ span: 24, order: 1 }}>
          {url ? (
            <a href={url} alt={url} target="_blank" rel="noreferrer noopener">
              <StyledImg sizes={{ ...coverImage.childImageSharp.fluid, aspectRatio: 1.5 }} alt={title} />
            </a>
          ) : (
            <Img fluid={coverImage.childImageSharp.fluid} alt={title} />
          )}
          </Col>
        </Row>
      ))}
    </Container>
  );
};

export default Recent;
