import React from 'react';
import styled from 'styled-components';
import { Layout, Row, Col, Affix } from 'antd';

// utils

// assets
import LogoImg from '../images/logo.png';

// actions

// components

// self-defined-components
const StyledHeader = styled(Layout.Header)`
  padding: 0 120px;
  background-color: #333333;
  user-select: none;

  @media (max-width: 992px) {
    padding: 0 40px;
  }

  @media (max-width: 768px) {
    padding: 0 30px;
  }

  @media (max-width: 576px) {
    padding: 0 20px;
  }
`;

const StyledLogo = styled.div`
  width: 64px;
  height: 64px;

  img {
    height: 100%;
    display: block;
    object-fit: cover;
  }
`;

const Menu = styled.div`
  color: white;
  font-size: 16px;
  line-height: 1;
  height: 64px;

  ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
  }

  li {
    display: flex;
    align-items: center;
    padding: 0 16px;
    height: 100%;
    transition: background-color 0.2s;


    &:hover {
      background-color: #2b9bfb;
      cursor: pointer;
    }
  }
`;

const AppHeader = () => {
  const handleClick = (anchor) => {
    console.log(anchor);
    document.querySelector(`#${anchor}`).scrollIntoView({
      behavior: 'smooth'
    });
  };

  return (
    <Affix>
      <StyledHeader>
        <Row>
          <Col span={8}>
            <StyledLogo>
              <img src={LogoImg} alt="logo" />
            </StyledLogo>
          </Col>
          <Col span={16}>
            <Menu>
              <ul>
                <li onClick={() => handleClick('about-me')}>About Me</li>
                <li onClick={() => handleClick('recent')}>Recent</li>
              </ul>
            </Menu>
          </Col>
        </Row>
      </StyledHeader>
    </Affix>
  );
};

export default AppHeader;
