import React, { useState } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Img from 'gatsby-image';
import Lightbox from 'react-image-lightbox';

import 'react-image-lightbox/style.css';

// utils

// assets

// actions

// components

// self-defined-components
const Cover = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  padding: 5px;
  font-size: 20px;
  font-weight: 600;
  color: white;
  text-align: center;
  cursor: pointer;
  user-select: none;

  img {
    filter: brightness(70%);
  }

  &:hover {
    img {
      filter: brightness(40%);
    }
  }
`;

const CoverTitle = styled.div`
  position: absolute;
  padding: 25px 30px;
  text-align: center;
  width: 100%;
`;

const CaptionContainer = styled.div`
  color: white;
  text-align: center;
  padding: 20px 40px;
`;

const Title = styled.div`
  font-size: 24px;
  font-weight: bold;
  line-height: 1;
`;
const Description = styled.div`
  font-family: 'ibm plex serif';
  line-height: 1;
  margin-top: 10px;
  word-break: break-word;
`;

const Caption = ({ title, description }) => (
  <CaptionContainer>
    {title && <Title>{title}</Title>}
    {description && <Description>{description}</Description>}
  </CaptionContainer>
);

Caption.propTypes = {
  title: PropTypes.string,
  description: PropTypes.string,
};
Caption.defaultProps = {
  title: '',
  description: '',
};

const Album = ({ title, coverFluid, coverTitle, description, images }) => {
  const [photoIndex, setPhotoIndex] = useState(0);
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      <Cover onClick={() => setIsOpen(true)}>
        <Img
          sizes={{ ...coverFluid, aspectRatio: 1.5 }}
          style={{ width: '100%' }}
          imgStyle={{ transition: 'filter 0.3s' }}
        />
        <CoverTitle>{coverTitle}</CoverTitle>
      </Cover>

      {isOpen && (
        <Lightbox
          imageCaption={<Caption title={title} description={description} />}
          mainSrc={images[photoIndex]}
          nextSrc={images[(photoIndex + 1) % images.length]}
          prevSrc={images[(photoIndex + images.length - 1) % images.length]}
          enableZoom={false}
          wrapperClassName="customLightboxModal"
          onCloseRequest={() => setIsOpen(false)}
          onMovePrevRequest={() =>
            setPhotoIndex((photoIndex + images.length - 1) % images.length)
          }
          onMoveNextRequest={() =>
            setPhotoIndex((photoIndex + 1) % images.length)
          }
        />
      )}
    </>
  );
};

Album.propTypes = {
  title: PropTypes.string,
  coverFluid: PropTypes.object.isRequired,
  coverTitle: PropTypes.string,
  description: PropTypes.string,
  images: PropTypes.arrayOf(PropTypes.string).isRequired,
};
Album.defaultProps = {
  title: '',
  coverTitle: '',
  description: '',
};

export default Album;
